const apis = {
  '/archives': () => import('@/../src/obj/archives.json'),
  '/tags': () => import('@/../src/obj/tags.json'),
  '/categories': () => import('@/../src/obj/categories.json'),
  '/docs/course/alg-notes': () => import('@/../src/obj/docs/course/alg-notes.json'),
  '/docs/course/prml': () => import('@/../src/obj/docs/course/prml.json'),
  '/docs/course/dsp': () => import('@/../src/obj/docs/course/dsp.json'),
  '/docs/course/oop-notes': () => import('@/../src/obj/docs/course/oop-notes.json'),
  '/docs/course/computer-network': () => import('@/../src/obj/docs/course/computer-network.json'),
  '/docs/course/segment-tree': () => import('@/../src/obj/docs/course/segment-tree.json'),
  '/docs/course/probability': () => import('@/../src/obj/docs/course/probability.json'),
  '/docs/course/dp': () => import('@/../src/obj/docs/course/dp.json'),
  '/docs/course/graph': () => import('@/../src/obj/docs/course/graph.json'),
  '/docs/course/mathematical-analysis': () => import('@/../src/obj/docs/course/mathematical-analysis.json'),
  '/docs/course/algebra': () => import('@/../src/obj/docs/course/algebra.json'),
  '/docs/course/os': () => import('@/../src/obj/docs/course/os.json'),
  '/archives/2020': () => import('@/../src/obj/archives/2020.json'),
  '/tags/notes': () => import('@/../src/obj/tags/notes.json'),
  '/tags/cpp': () => import('@/../src/obj/tags/cpp.json'),
  '/tags/math': () => import('@/../src/obj/tags/math.json'),
  '/tags/operating-system': () => import('@/../src/obj/tags/operating-system.json'),
  '/tags/computer-network': () => import('@/../src/obj/tags/computer-network.json'),
  '/tags/divide-and-conquer': () => import('@/../src/obj/tags/divide-and-conquer.json'),
  '/tags/machine-learning': () => import('@/../src/obj/tags/machine-learning.json'),
  '/tags/probability': () => import('@/../src/obj/tags/probability.json'),
  '/tags/dp': () => import('@/../src/obj/tags/dp.json'),
  '/tags/graph': () => import('@/../src/obj/tags/graph.json'),
  '/tags/greedy': () => import('@/../src/obj/tags/greedy.json'),
  '/tags/data-structure': () => import('@/../src/obj/tags/data-structure.json')
}

function get(url) {
  if (apis[url])
    return new Promise((resolve, reject) => {
      apis[url]()
        .then(({default: d}) => resolve(d))
        .catch(e => reject(e));
    });
  else return Promise.reject(new Error(`${url} not found.`));
}
export default { get }
